* {
  box-sizing: border-box;
}
.App {
  max-width: 1200px;
  margin: 0 auto;
  display: grid;
  grid-template-areas:
  "header header header"
  "menu main main";
  grid-template-rows: 60px 1fr;
  grid-template-columns: 2fr 10fr;
  grid-gap: 10px;
}
.main {
  grid-area: main;
  background-color: brown;
}